<template>
  <div id="app">
    <div class="app-container">
      <!-- autocomplete -->
      <app-autocomplete class="elevated" :world-countries="worldCountries" @select="onSelect"></app-autocomplete>
      <!-- results -->
      <template v-if="userData.length > 0">
        <div v-for="(stats, index) of userData" :key="index">
          <app-statistics
            class="elevated"
            :statistics="stats"
            :favourite="userBookmarks.includes(stats.country.toLowerCase())"
            @pin="onPin"
            @unPin="onUnpin"
            @delete="onDelete"
          ></app-statistics>
        </div>
      </template>
      <!-- no data -->
      <template v-else>
        <div class="no-data">
          <div>No Countries Selected</div>
          <div>Add Some</div>
        </div>
      </template>
      <!-- flashMessage -->
      <app-toast :duration="1500" :message="flashMessage"></app-toast>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import AppAutocomplete from "./components/AppAutocomplete";
import AppStatistics from "./components/AppStatistics";
import AppToast from "./components/AppToast";

class Storage {
  constructor(name) {
    this.name = name || null;
  }

  getItems() {
    const data_serialized = localStorage.getItem(this.name);
    if (data_serialized) {
      return JSON.parse(data_serialized);
    } else {
      return null;
    }
  }

  setItems(items = []) {
    if (Array.isArray(items)) {
      localStorage.setItem(this.name, JSON.stringify(items));
      return true;
    } else {
      return false;
    }
  }

  addItem(value) {
    let entry = value
      .toString()
      .trim()
      .toLowerCase();
    let data = this.getItems();
    if (!data) {
      data = [entry];
    } else if (!data.includes(entry)) {
      data = [...data, entry];
    } else {
      return false;
    }
    this.setItems(data);
    return true;
  }

  removeItem(value) {
    let entry = value
      .toString()
      .trim()
      .toLowerCase();
    let data = this.getItems();
    if (!data) {
      return false;
    } else {
      data = data.filter(item => {
        return item !== entry;
      });
    }
    this.setItems(data);
    return true;
  }

  clearItems() {
    localStorage.clear();
  }
}

export default {
  name: "App",

  components: {
    AppAutocomplete,
    AppStatistics,
    AppToast
  },

  data() {
    return {
      flashMessage: "",
      worldCountries: [],
      userCountries: new Set(),
      userBookmarks: [],
      userData: [],
      storage: new Storage("bookmarks")
    };
  },

  methods: {
    onSelect(country) {
      this.userCountries = new Set(
        this.userCountries.add(country.toLowerCase())
      );
      const queryData = JSON.stringify(Array.from(this.userCountries));
      const query = `
      query statsQuery {
          stats(countries: ${queryData}) {
            country
            cases
            deaths
            active
            critical
            recovered
            todayCases
            todayDeaths
            casesPerOneMillion
          }
        } 
      `;
      axios({
        url: "/graphql",
        method: "post",
        data: { query: query }
      }).then(result => {
        const { data } = result.data;
        this.userData = data.stats;
      });
    },

    onDelete(country) {
      if (this.userCountries.delete(country.toLowerCase())) {
        this.userCountries = new Set(this.userCountries);
        this.userData = this.userData.filter(item => {
          return item.country.toLowerCase() != country.toLowerCase();
        });
      }
    },

    onPin(country) {
      if (this.storage.addItem(country)) {
        this.userBookmarks = this.storage.getItems();
        this.flashMessage = `${country} added from Favourites`;
      }
    },

    onUnpin(country) {
      if (this.storage.removeItem(country)) {
        this.userBookmarks = this.storage.getItems();
        this.flashMessage = `${country} removed from Favourites`;
      }
    }
  },

  created() {
    const query = `
      query countryQuery {
        countries {
          country
        }
      }`;
    axios({
      url: "/graphql",
      method: "post",
      data: { query: query }
    }).then(result => {
      const { data } = result.data;
      this.worldCountries = data.countries.map(item => {
        return item.country.toLowerCase();
      });
    });
  },

  mounted() {
    const bookmarks = this.storage.getItems();
    if (bookmarks) {
      this.userCountries = new Set([...bookmarks]);
      this.userBookmarks = [...bookmarks];
      const query = `
        query statsQuery {
            stats(countries: ${JSON.stringify(bookmarks)}) {
              country
              cases
              deaths
              active
              critical
              recovered
              todayCases
              todayDeaths
              casesPerOneMillion
            }
          }
        `;
      axios({
        url: "/graphql",
        method: "post",
        data: { query: query }
      }).then(result => {
        const { data } = result.data;
        this.userData = data.stats;
      });
    }
  }
};
</script>

